body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  height: 100vh;
}

.App-header {
  background-color: #03A9F4;
  padding: 10px;
  color: white;
}

.App-header-container {
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
}

.App-title {
  font-size: 26px;
}

.App-intro {
  font-size: large;
}

.App-container {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  -webkit-flex-flow: column;
          flex-flow: column;
  display: -webkit-flex;
  display: flex;
}

.App-messages {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 20 1 auto;
          flex: 20 1 auto;
  -webkit-flex-direction: column-reverse;
          flex-direction: column-reverse;
  background-color: white;
  border-bottom: 2px solid gray;
  padding: 20px;
  overflow-y: scroll;
}

.App-moniker {
  padding: 20px;
  border-bottom: 2px solid gray;
}

.App-message-entry {
  padding: 20px;
  padding-bottom: 0;
  display: -webkit-flex;
  display: flex;
}

.App-message-entry input {
  border: 1px solid #ced4da;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  border-radius: 8px;
  font-size: 20px;
}

.App-submit-button {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
}

.App-submit-button input {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  height: 40px;
  font-size: 24px;
  background-color: #007bff;
  border-color: #007bff;
  border: 1px solid transparent;
  border-radius: 8px;
  color: white;
}
